@import url(https://cdn.jsdelivr.net/gh/mrspecht/cdn/reset.css);

:root {
    --primary-background: rgb(248, 249, 254);
    --primary-color: #4F33FF;
}

.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

body {
    font-family: 'Nunito Sans', 'Open Sans', Arial, sans-serif;
}

.flex {
    display: flex;
}

.space-between {
    justify-content: space-between;
}

.gap-10 {
    gap: 10px;
}

.gap-15 {
    gap: 15px;
}

/*---------------------------------------------------------------*/
/*-------------------Header--------------------------------------*/
/*---------------------------------------------------------------*/

header {
    width: 100%;
    height: 56px;
    background-color: var(--primary-color);
}

header h1 {
    text-transform: uppercase;
    color: #fff;
    font-weight: 800;
    line-height: 56px;
}

ul {
    gap: 20px;
}

nav ul li a {
    color: rgb(255 255 255);
    font-weight: 700;
    font-size: 18px;
    text-decoration: none;
    cursor: pointer;
    transition: color 0.25s ease-in-out;
    line-height: 56px;
    transition: all 0.25s ease-in-out;
}

nav ul li a:hover {
    text-decoration: underline;
}

/*---------------------------------------------------------------*/
/*-------------------Hero Banner---------------------------------*/
/*---------------------------------------------------------------*/

.hero {
    position: relative;
    background: #191824 url('../media/mixing.jpg') center 25% / cover no-repeat;
    width: 100%;
    height: 550px;
    display: grid;
    place-items: center;
}

.hero::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 0.6);
    content: '';
}

.hero h2, .hero p{
    color: #fff;
}

.hero-text {
    position: relative;
    width: 600px;
    bottom: 4px;
    text-align: center;
    z-index: 9999;
}

.hero-text h2 {
    font-size: 60px;
    font-weight: 700;
    line-height: 1;
}

.hero-text p {
    margin-top: 5px;
    font-size: 20px;
}

.hero-text div {
    margin-top: 30px;
    justify-content: center;
    gap: 10px;
}

button {
    display: block;
    width: 140px;
    padding: 14px 0;
    background-color: var(--primary-color);
    color: #fff;
    border-radius: 50px;
    font-weight: 700;
    text-decoration: none;
    transition: all 0.25s ease-in-out;
}

button:hover {
    background-color: #fff;
    color: var(--primary-color);
}

.secondary {
    background-color: transparent;
    border: 1px solid var(--primary-color);
    color: var(--primary-color);
}

.secondary:hover {
    background-color: var(--primary-color);
    color: #fff;
}

.about {
    margin: 60px 0px;
}

.about-div {
    background-color: #fff;
    padding: 15px;
    border-radius: 6px;
}

.about p {
    font-size: 17px;
    font-weight: 500;
}

article {
    width: 50%;
}

article span {
    color: var(--primary-color);
}

article button {
    margin-top: 10px;
}

article button:hover {
    color: #4F33FF;
}

aside {
    width: 50%;
}

.aside-pic {
    height: 400px;
    width: 100%;
    background: #f1f4f6 url('../media/stage.jpg') center 25% / cover no-repeat;
}

/*---------------------------------------------------------------*/
/*-------------------Gallery-------------------------------------*/
/*---------------------------------------------------------------*/

.gallery {
    margin: 60px 0px;
    text-align: center;
}

.gallery h3 {
    font-size: 40px;
}

.gallery span {
    color: var(--primary-color);
}

.gallery .albums {
    margin-top: 10px;
}

.gallery figure {
    flex-grow: 1;
    transition: all 0.25s ease-in-out;
}

.gallery img {
    border-radius: 5px;
    cursor: pointer;
    height: 280px;
    width: 1000px;
    flex-grow: 1;
    transition: all 0.25s ease-in-out;
}

.gallery img:hover {
    width: 1080px;
}

/*---------------------------------------------------------------*/
/*-------------------Footer--------------------------------------*/
/*---------------------------------------------------------------*/

footer {
    height: 100px;
    background-color: #acacac;
}

footer p {
    color: #000;
    line-height: 100px;
    font-size: 17px;
    font-weight: 600;
}

.list {
    height: 100%;
}

.list ul {
    gap: 10px;
}

.list ul li {
    line-height: 100px;
    list-style: none;
}

.list i {
    font-size: 25px;
    color: #000;
}

.list ul li a i {
    text-decoration: none;
    cursor: pointer;
    transition: color 0.25s ease-in-out;
}

.list ul li a i:hover{
    color: var(--primary-color);
}